import React, { Component } from "react";
import { DateRangePicker } from "react-dates";
import {
  faBed,
  faDollarSign,
  faCocktail,
} from "@fortawesome/free-solid-svg-icons";
import Popup from "reactjs-popup";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Map from "./components/Map";
import "bootstrap/dist/css/bootstrap.css";
import "react-dates/lib/css/_datepicker.css";
import "reactjs-popup/dist/index.css";
import "react-dates/initialize";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: null,
      endDate: null,
      focusedInput: null,
      valueField: "rooms",
    };

    this.updateNavSelection = this.updateNavSelection.bind(this);
  }

  updateNavSelection(fieldName) {
    this.setState({ valueField: fieldName });
  }

  renderNavButton(icon, title, fieldName) {
    const tabToggleClass =
      fieldName === this.state.valueField ? "tab--active" : "tab";
    const tabClass = `${tabToggleClass} my-auto d-md-inline-block`;

    return (
      <a
        className={tabClass}
        href="#"
        ref={fieldName}
        onClick={() => this.updateNavSelection(fieldName)}>
        <FontAwesomeIcon icon={icon} />
        <span className="px-2">{title}</span>
      </a>
    );
  }

  render() {
    return (
      <div className="outer__container">
        <nav className="site-header sticky-top py-1">
          <div className="container d-flex flex-column flex-md-row justify-content-between">
            <img
              src={`${process.env.PUBLIC_URL}/HE_Logo_White.png`}
              alt="Hotel Engine logo"
              className="my-auto d-none d-md-none d-lg-inline-block"
            />
            <img
              src={`${process.env.PUBLIC_URL}/HE_Badge.png`}
              alt="Hotel Engine logo"
              className="my-auto d-none d-md-inline-block d-lg-none fixed"
            />
            <h2 className="blue-text py-2 d-md-inline-block">ChartTrek</h2>
            {this.renderNavButton(faCocktail, "Bookings", "rooms")}
            {this.renderNavButton(faBed, "Room nights", "room_nights")}
            {this.renderNavButton(faDollarSign, "Revenue", "revenue")}
          </div>
        </nav>
        <div className="map__container">
          <Map
            startDate={this.state.startDate}
            endDate={this.state.endDate}
            valueField={this.state.valueField}
          />
        </div>
        <div
          style={{
            backgroundImage: `url(${process.env.PUBLIC_URL}/heroImage.jpg)`,
            backgroundBlendMode: "hard-light",
            backgroundColor: "#0c64f2",
            backgroundSize: "cover",
            overflow: "hidden",
          }}>
          <div className="m-4 date-picker__container">
            <DateRangePicker
              startDateId="startDate"
              endDateId="endDate"
              startDate={this.state.localStart}
              endDate={this.state.localEnd}
              onDatesChange={({ startDate, endDate }) => {
                this.setState({ localStart: startDate, localEnd: endDate });
              }}
              focusedInput={this.state.focusedInput}
              onFocusChange={(focusedInput) => {
                this.setState({ focusedInput });
              }}
              isOutsideRange={() => false}
              withPortal={true}
              onClose={({ startDate, endDate }) => {
                console.log(startDate);
                console.log(endDate);
                this.setState({ startDate, endDate });
              }}
            />
          </div>
          <footer className="my-auto py-2">
            <p className="m-0">
              <Popup trigger={<span>Hotel Engine. </span>} position="top center">
                <div className="popup__image">
                  <img src={`${process.env.PUBLIC_URL}/maskItSo.png`} alt="Picard" className="popup__image"/>
                </div>
              </Popup>
              <span>&copy; {new Date().getFullYear()}. Created during the 2020 ðŸ¦  while wearing </span>
              <Popup trigger={<span> ðŸ˜·.</span>} position="right center" id="yolo">
                YOLO!!!
              </Popup>
            </p>
          </footer>
        </div>
      </div>
    );
  }
}

export default App;
