let bars = [];
var material = new window.THREE.MeshBasicMaterial({
  color: "#fff",
  transparent: true,
});
var highlightmaterial = new window.THREE.MeshBasicMaterial({
  color: "yellow",
  transparent: true,
});

export function addBars(threeLayer, data) {
  // normalize max to 1000000
  const maxHeight = data.reduce((max, row) => (max > row.height ? max : row.height), 1);
  const multiplier = 1000000 / maxHeight;
  // console.log(`data: `, data);
  // if (bars.length) {
  //   threeLayer.removeMesh(bars);
  // }
  bars = data.map(function (d) {
    // console.log(`d: `, d);
    const bar = threeLayer.toBar(
      d.coordinate,
      {
        height: d.height * multiplier,
        radius: 7500,
        topColor: "#fff",
        // radialSegments: 4
      },
      material
    );

    // tooltip test
    bar.setToolTip(d.title, {
      showTimeout: 0,
      eventsPropagation: true,
      dx: 10,
    });

    //infowindow test
    bar.setInfoWindow({
      content: d.description,
      title: d.title,
      animationDuration: 0,
      autoOpenOn: false,
    });

    //event test
    [
      "click",
      "mouseout",
      "mouseover",
      "mousedown",
      "mouseup",
      "dblclick",
      "contextmenu",
    ].forEach(function (eventType) {
      bar.on(eventType, function (e) {
        // console.log(e.type, e);
        // console.log(this);
        if (e.type === "mouseout") {
          this.setSymbol(material);
        }
        if (e.type === "mouseover") {
          this.setSymbol(highlightmaterial);
        }
      });
    });

    return bar;
  });
  // console.log(`bars.length: `, bars.length);
  // threeLayer.addMesh(bars);
  // threeLayer.config("animation", true);
  return bars;
}
