const groupedByDate = bookingData.reduce((acc, row) => {
  if (acc[row[DATE_INDEX]]) {
    acc[row[DATE_INDEX]].push(row);
  } else {
    acc[row[DATE_INDEX]] = [row];
  }
  return acc;
}, {});

